@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
:root {
	--background: rgb(40, 40, 40);
	--background-d: rgb(35, 35, 35);
	--background-l: rgb(68, 68, 68);
	--foreground: rgb(236, 236, 236);
	--gradient: linear-gradient(30deg, #e99700, #ff0015);
	--gradient-average: #e95d00;
	--muted: rgb(155, 155, 155);
}
body {
	background-color: rgb(40, 40, 40);
	background-color: var(--background);
	margin: 0;
	color: rgb(236, 236, 236);
	color: var(--foreground);

	font-family: "Ubuntu", sans-serif;
}
hr {
	color: rgba(0, 0, 0, 0.35);
	border-bottom: none;
}
a {
	color: unset;
	text-decoration: none;
	outline: none;
}

.center {
	text-align: center;
}

.diagonalbg {
	position: fixed;
	top: -60px;
	left: -15px;
	transform: rotateZ(45deg);
	background: linear-gradient(30deg, #e99700, #ff0015);
	background: var(--gradient);
	height: 100px;
	width: 50px;
}

.navbar {
	display: flex;
	flex-direction: row;
	padding-left: 3vw;
}
.logo {
	margin-top: auto;
	margin-bottom: auto;
}
.logo > img {
	margin-top: 6px;
	width: max(6vw, 36px);
	height: auto;
	pointer-events: none;
}
.navbar-links {
	position: relative;
	display: inherit;
}
.navbar-links > div:not(.logo):not(.sliding-underline) {
	padding: max(4.5vw, 20px);
	padding-left: max(2vw, 10px);
}
.navbar-links > div:nth-child(1) {
	padding-left: max(3.5vw, 20px) !important;
}
.navbar-links > div > a {
	font-weight: bold;
	letter-spacing: 0.8px;
	font-size: 21px;
	cursor: pointer;
	color: rgb(236, 236, 236);
	color: var(--foreground);
	transition: color 100ms linear;
	background: linear-gradient(30deg, #e99700, #ff0015);
	background: var(--gradient);
	-webkit-background-clip: text;
	        background-clip: text;
}
.navbar-links > div > a:hover {
	color: transparent;
	transition: color 100ms linear;
}

.page-container {
	width: 100%;
	max-width: 100vw;
}

.current-section {
	position: relative;
}

.header {
	margin-top: 0;
	font-size: 70px;
	margin-bottom: 26px;
}

.paragraph {
	font-size: 19px;
	line-height: 32px;
	font-weight: 400;
	letter-spacing: 0.5px;
}
@media only screen and (max-width: 560px) {
	.header {
		font-size: 56px;
	}
	.paragraph {
		font-size: 14px;
		line-height: 22px;
	}
}
.paragraph.muted {
	color: rgb(155, 155, 155);
	color: var(--muted);
}

/* Share icons */

.share {
	position: fixed;
	right: 16px;
	bottom: 16px;
}
.share-container {
	display: flex;
	flex-direction: column;
}
.share-container > * {
	position: relative;
	text-align: end;
	margin-top: 14px;
	transition: margin-right 200ms ease-out;
}
.share-container > *::after {
	position: absolute;
	top: -6.5px;
	bottom: -6.5px;
	left: -6.5px;
	right: -6.5px;
	content: " ";
}
.share-container > *:hover {
	transition: margin-right 200ms ease-out;
	margin-right: 6px;
}

.share-container > *::after {
	color: rgb(68, 68, 68);
	color: var(--background-l);
}

.share-container-expand > * {
	margin: 38px;
	transition: transform 300ms linear, margin 400ms ease-in;

	-webkit-backface-visibility: hidden;

	        backface-visibility: hidden;
	perspective: 1000;
	transform: translateZ(0) scale(3);
}
.share-container-expand::after {
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	perspective: 1000;

	box-shadow: 0 0 0 150vw;
	border-radius: 50%;
	color: white;
	transition: box-shadow 700ms ease-in, color 700ms ease-in;
	z-index: 99999;
	transform: translateZ(1);
}

@media only screen and (max-width: 510px) {
	.share {
		right: 0;
		bottom: 0;
		left: 0;
		padding: 8px;
		background-color: rgb(35, 35, 35);
		background-color: var(--background-d);
	}
	/* Mobile keyboard fix */
	@media (max-height: calc(100vw * 1.5)) {
		.share {
            /* display: none !important; */
            position: static;
            margin-top: 8vw;
		}
	}
	.share-container {
		flex-direction: row;
	}
	.share-container > * {
		margin: 0;
		margin-left: 2px;
		margin-right: 14px;
	}
}

.contact-container {
	margin-top: 8.5vh;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-flow: row;
	justify-content: space-evenly;
	width: max(60vw, 768px);
	max-width: 100%;
	padding: 10px;
	box-sizing: border-box;
}
.contact-container > .info {
	position: relative;
	top: -3vw;
	margin-top: auto;
	margin-bottom: auto;
	margin-right: 20px;
	max-width: 26vw;
}

.break-word {
	word-wrap: normal;
}

.thin {
	padding: 8px !important;
	font-size: 18px !important;
	margin-bottom: 8px !important;
}

.form {
	position: relative;
	margin-top: 25px;
	background-color: var(--background-d);
	border-radius: 15px;
	padding: 16px;
	padding-top: 4%;
	padding-bottom: 0;
	max-width: 29vw;
}

.form > div {
	margin-bottom: 12px;
}
.form > div > input,
.form > div > textarea {
	margin-bottom: 10px;
	margin-left: 10px;
	margin-right: 10px;
	padding: 12px;
	border: none;
	border-radius: 5px;
	background-color: var(--background-l);
	outline: none;
	font-size: 20px;

	color: white;

	font-family: "Ubuntu", sans-serif;
}
.field-error {
	-webkit-animation: fade-in 100ms linear;
	        animation: fade-in 100ms linear;
	max-width: 92%;
	text-align: end;
	font-size: 13px;
	margin-left: auto;
	position: relative;
	right: 20px;
	top: -0.5vw;
	color: tomato;
}

/* Email info */
.email-info {
	display: flex;
	flex-direction: column;
}

/* Personal Info */
.personal-info {
	display: flex;
	justify-content: space-around;
}
.personal-info > input {
	max-width: 140px;
}

/* Question info */
.question-info {
	display: flex;
	flex-direction: column;
}
.question-info > textarea {
	resize: none;
	max-height: 35vh;
	min-height: 4em;
    height: 4em;
    overflow-y: auto !important;
}

/* Success element */
.submit-success {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
}
.submit-success > div {
	font-size: 6vh;
	margin-bottom: 80px;
	-webkit-animation: slide-up 1100ms cubic-bezier(0.77, 0, 0.175, 1);
	        animation: slide-up 1100ms cubic-bezier(0.77, 0, 0.175, 1);
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}
.blur {
	-webkit-filter: blur(7px) brightness(50%);
	        filter: blur(7px) brightness(50%);
}

/* Footer */
.footer {
	display: flex;
	justify-content: flex-end;
	margin-top: 20px;
}
.footer > .submit-button {
	cursor: pointer;
	padding: 9px;
	margin: 14px;
	margin-top: 0;
	border: none;
	border-radius: 5px;
	outline: none;
	font-size: 20px;
	width: 150px;
	color: white;
	background: var(--gradient);
}
.footer > .submit-button:hover {
	transition: box-shadow 50ms linear;
	box-shadow: 0 0 5px 0 var(--gradient-average);
}
.footer > .submit-button:active {
	transform: translateY(1px);
}

@media only screen and (max-width: 510px) {
	.submit-success > div {
		font-size: 14vw;
		margin-bottom: 80px;
	}
	.contact-container {
		flex-flow: column;
		margin-left: 0;
		text-align: center;
	}
	.contact-container > * {
		padding-left: 2vw;
		padding-right: 2vw;
	}
	.contact-container > .info {
		position: unset;

		padding: 0;
		margin-left: 0;
		margin-right: 0;
		margin-bottom: 1.5vw;
		width: 100%;
		max-width: 100%;
		word-wrap: break-word;
	}
	.info > .header {
		margin-bottom: 8vw !important;
	}
	.form {
		max-width: 100%;
	}
	.personal-info > input {
		width: 37%;
	}

	.footer > .submit-button {
		width: 100%;
	}
}

@-webkit-keyframes slide-up {
	0% {
		opacity: 0;
		transform: translateY(25px) scale(0.95);
    }
	40% {
		opacity: 1;
		transform: none;
	}
	80% {
		opacity: 1;
		transform: none;
	}
	100% {
		opacity: 0;
		transform: translateY(-25px) scale(0.95);
	}
}

@keyframes slide-up {
	0% {
		opacity: 0;
		transform: translateY(25px) scale(0.95);
    }
	40% {
		opacity: 1;
		transform: none;
	}
	80% {
		opacity: 1;
		transform: none;
	}
	100% {
		opacity: 0;
		transform: translateY(-25px) scale(0.95);
	}
}

.home-container {
	margin-top: 7vh;
	margin-left: 16vw;
	width: 35vw;
}

.learn-more {
	display: flex;
	flex-direction: row;
	cursor: auto;
	cursor: initial;
	font-size: 18px;
	font-weight: bold;
	margin-top: 30px;
	letter-spacing: 0.5px;
	margin-left: 0.25vw;
}

.play-o {
	cursor: pointer;
	border-radius: 50%;
	background: var(--gradient);
	width: 56px;
	height: 56px;
	margin-right: 15px;
	transition: margin-right 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.play-o:hover {
	transition: margin-right 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
	margin-right: 20px;
}
.play-o:hover > .play-i > .play-svg {
	transform: translateY(1px);
}

.play-i {
	position: relative;
	border-radius: 50%;
	margin: 4px;
	width: 48px;
	height: 48px;
	background-color: var(--background);
	box-shadow: 0 0 10px 2px transparent;

	display: flex;
}
.play-o:hover > .play-i {
	box-shadow: 0 0 12px 1px #ff6600;
}
.play-svg {
	display: block;
	margin: auto;
	width: 26px;
	height: 26px;

	position: relative;
	left: 3px;
}

.learn-more > .label {
	margin-block: auto;
}

@media only screen and (max-width: 560px) {
	.home-container {
		padding-left: 4vw;
		padding-right: 4vw;
		margin-left: 0;
		margin-right: 0;
		width: auto;
		flex-flow: column;
		margin-left: 0;
		text-align: center;
	}
	.learn-more {
		margin-top: 20px;
	}
}

.fade-in {
	-webkit-animation: slide 250ms, fade-in 250ms;
	        animation: slide 250ms, fade-in 250ms;
}
.fade-out {
	-webkit-animation: fade-in 250ms;
	        animation: fade-in 250ms;
	animation-direction: reverse;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes fade-in {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes fade-in {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@-webkit-keyframes slide {
	from {
		transform: translateX(-1vw);
	}
	to {
		transform: translateX(0);
	}
}

@keyframes slide {
	from {
		transform: translateX(-1vw);
	}
	to {
		transform: translateX(0);
	}
}

